@import "../../libs/styles/main.scss";

.wrapper {
  border-radius: 20px;
  background-color: $color-white;
  padding: $general-spacing-unit * 5;
  box-shadow: 0 2px 20px 0 rgba(27, 155, 158, 0.1),
    0 2px 20px 0 rgba(151, 151, 151, 0.1);
  box-sizing: border-box;
  margin-bottom: $general-spacing-unit * 5;
  display: block;
  width: 100%;
}

.header {
  font-size: 32px;
  line-height: 38px;
  text-align: left;
  margin-bottom: $general-spacing-unit * 4;

  @include breakpoint("tablet") {
    text-align: center;
    margin-bottom: $general-spacing-unit;
  }
}

.formWrapper {
  display: flex;
  text-align: left;
  flex-wrap: wrap;

  fieldset,
  &>div {
    margin-right: $general-spacing-unit * 2;
    width: max-content;
    flex-basis: 235px;
  }

  fieldset:nth-child(1) {
    flex-basis: 288px;
  }


  @include breakpoint("tablet") {
    justify-content: center;
    align-items: end;

    fieldset,
    &>div {
      flex-basis: 288px;
      margin-top: $general-spacing-unit * 3;
    }
  }

  @include breakpoint("small-tablet-bk") {
    flex-direction: column;
    align-items: center;

    fieldset,
    &>div,
    fieldset:nth-child(1) {
      flex-basis: max-content;
      width: 295px;
    }
  }

}

.search {
  display: flex;
  align-items: center;
  padding: 0 $general-spacing-unit * 3;
  justify-content: center;
  max-width: $general-spacing-unit * 36;

  svg {
    height: 18px;
    margin-right: $general-spacing-unit;
  }

  @include breakpoint("tablet") {
    margin-right: $general-spacing-unit * 2;
  }
}

.cancel {
  background: transparent;
  color: $color-bali-hai;
  border: 0;
  width: max-content;
  padding: 0;
  display: none;
  margin-top: $general-spacing-unit;

  &:hover {
    background: transparent;
  }

  @include breakpoint("small-tablet-bk") {
    display: block;
  }
}