@import "../../libs/styles/main.scss";

.container {
    width: 68%;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include breakpoint("small-tablet-bk") {
        width: 100%;
    }
}

.wrapper {
    background-color: $color-white;
    padding: $general-spacing-unit * 3;
    width: 100%;
    box-sizing: border-box;

    @include breakpoint("mobile") {
        padding: $general-spacing-unit * 5 $general-spacing-unit * 4;
    }
}

.showMore {
    background: transparent;
    color: $color-surfie-green;
    max-width: 350px;
    max-height: 68px;

    &:hover {
        color: $color-white;
    }

    @include breakpoint("small-tablet-bk") {
        margin: 0;
        align-self: auto;
        width: calc(100% - $general-spacing-unit * 8);
    }
}