@import "../../libs/styles/main.scss";

.wrapper {
  height: 100%;
  background-color: $color-white;
  padding: 20px;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  padding-top: $general-spacing-unit * 7;
  display: flex;
  width: 100%;
}

.header {
  font-size: 32px;
  line-height: 38px;
  text-align: center;
  margin-bottom: $general-spacing-unit * 4;
}

.formWrapper {
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: center;

  fieldset {
    margin-bottom: $general-spacing-unit * 3;
  }
}

.search,
.searchSecondLayout {
  display: flex;
  align-items: center;
  padding: 0 $general-spacing-unit * 3;
  justify-content: center;
  max-width: $general-spacing-unit * 36;
  margin: 0;

  svg {
    height: 18px;
    margin-right: $general-spacing-unit;
  }
}

.searchSecondLayout {
  background: transparent;
  color: $color-surfie-green;
  margin: 0;

  &:hover {
    background: transparent;
  }
}

.cancel {
  background: transparent;
  color: $color-bali-hai;
  border: 0;
  width: max-content;
  padding: 0;
  margin-top: $general-spacing-unit;
  display: block;

  &:hover {
    background: transparent;
  }
}

.searchMobile {
  background-color: $color-ocean;
  padding: $general-spacing-unit * 3;
  align-items: center;
  justify-content: center;
  width: 100%;
  box-sizing: border-box;
  display: flex;
}

.closeButton {
  background: transparent;
  padding: 0;
  margin: 0;
  border: 0;
  display: flex;
  cursor: pointer;
  position: absolute;
  top: 11px;
  right: 11px;
}