@import "../../libs/styles/main.scss";

.wrapper {
  display: flex;
  flex-direction: column;
  margin-right: $general-spacing-unit * 2;
  flex-basis: 235px;

  @include breakpoint("mobile") {
    flex-basis: max-content;
    margin: 0;
    margin-bottom: $general-spacing-unit * 3;
    width: 295px;
  }
}

.title {
  display: block;
  font-weight: 700;
  font-size: 12px;
  color: $color-cloud-burst;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: $general-spacing-unit;
}

.selectContainer {
  position: relative;

  svg {
    height: $general-spacing-unit * 2;
    position: absolute;
    right: 14px;
    top: calc(50% - $general-spacing-unit);
  }
}

.select {
  font-family: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu", sans-serif;
  font-weight: 600;
  font-size: 18px;
  color: $color-bali-hai;
  line-height: 24px;
  border-radius: 10px;
  border: 1px solid $color-soft-white;
  background: $color-lilac;
  outline: none;
  width: 100%;
  padding: 14px $general-spacing-unit * 2;
  box-sizing: border-box;
  resize: vertical;
  min-height: 56px;
  appearance: none;
  position: relative;
}