@import "../../../libs/styles/main.scss";

.filterMobile {
    margin-top: $general-spacing-unit * 3;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border-bottom: solid 1px $color-light-grey;
    padding-bottom: $general-spacing-unit * 2;
    width: 100%;
    max-height: $general-spacing-unit * 2;
    overflow: hidden;
    transition: max-height .5s ease-in;
}

.filterNameMobile {
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    color: $color-surfie-green;
}

.filterValue {
    display: flex;
    align-items: center;
    margin-top: $general-spacing-unit * 2;

    span:nth-child(3) {
        margin-left: calc($general-spacing-unit / 2);
    }
}

.checkBox {
    label:before {
        margin-right: $general-spacing-unit;
        border: solid 1px $color-soft-grey;
        background-color: $color-white;
    }
}

.loadMore {
    background: transparent;
    color: $color-surfie-green;
    height: 32px;
    width: 128px;
    font-size: 13px;
    line-height: 20px;
    padding: 6px 10px;

    &:hover {
        background: transparent;
        color: $color-surfie-green;
    }
}

.buttonExpand {
    background-color: transparent;
    border: 0;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;

    svg {
        width: 14px;
        transition: all .3s;
    }
}