@import "../../libs/styles/main.scss";

.wrapper,
.Secondwrapper {
    background-color: $color-ocean;
    padding: $general-spacing-unit * 3 $general-spacing-unit * 7;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    box-sizing: border-box;

    * {
        user-select: none;
    }
}

.Secondwrapper {
    padding-top: 0;
}

.searchSecondLayout {
    display: flex;
    align-items: center;
    padding: 0 $general-spacing-unit * 3;
    justify-content: center;
    max-width: $general-spacing-unit * 36;
    margin: 0;
    background: transparent;
    color: $color-surfie-green;
    margin: 0;

    svg {
        height: 22px;
        margin-right: $general-spacing-unit;
    }

    @include breakpoint("tablet") {
        margin-right: $general-spacing-unit * 2;
    }

    &:hover {
        background: transparent;
    }
}

.title {
    margin: 0 0 8px 0;
    width: 100%;
    text-align: left;
}

.cancel {
    background: transparent;
    color: $color-bali-hai;
    border: 0;
    width: max-content;
    padding: 0;
    margin-top: $general-spacing-unit;

    &:hover {
        background: transparent;
    }
}

.filtersAppliedContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    span {
        font-weight: 600;
        font-size: 16px;
        line-height: 18px;
        margin-bottom: 16px;
    }
}

.filtersApplied {
    display: flex;
    flex-wrap: wrap;
}

.reset {
    background: transparent;
    border: 0;
    font-size: 13px;
    color: $color-surfie-green;
    font-weight: 700;
    margin-top: 12px;
}