@import "../../libs/styles/main.scss";

.wrapper {
    background-color: $color-ocean;
    width: 32%;
    padding: $general-spacing-unit * 3;
    max-width: 350px;
    border-radius: 10px 0 0 10px;

    @include breakpoint("small-tablet-bk") {
        display: none;
    }
}

.title {
    line-height: 24px;
    text-align: left;
    margin: 0 0 $general-spacing-unit * 4.3;
}