@import "../../libs/styles/main.scss";

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px 0;

    * {
        user-select: none;
    }

    @include breakpoint("mobile") {
        padding: 0 50px 40px 50px;
    }
}

.image {
    height: 276px;

    @include breakpoint("mobile") {
        height: 250px;
    }
}

.header {
    font-size: 48px;
    line-height: 48px;
    color: $color-eastern-blue;
    margin: $general-spacing-unit * 3 0 $general-spacing-unit * 2;

    b {
        color: $color-cloud-burst;
    }

    @include breakpoint("mobile") {
        font-size: 32px;
        line-height: 38px;
        margin-bottom: 8px;
    }
}

.description {
    font-size: 20px;
    line-height: 28px;
    color: $color-plantation;
    text-align: center;
    margin-bottom: $general-spacing-unit * 3;
    margin-top: 0;
    max-width: 412px;

    @include breakpoint("mobile") {
        font-size: 18px;
        line-height: 28px;
    }
}

.search {
    display: flex;
    align-items: center;
    padding: 0 $general-spacing-unit * 3;
    justify-content: center;
    max-width: $general-spacing-unit * 36;
    margin: 0;

    svg {
        height: 18px;
        margin-right: $general-spacing-unit;
    }

    @include breakpoint("tablet") {
        margin-right: $general-spacing-unit * 2;
    }
}