@import "../../../libs/styles/main.scss";

.wrapper {
    display: flex;
    flex-direction: column;
    border-bottom: solid 1px $color-soft-white;
    width: 100%;
    text-align: left;
    padding-bottom: $general-spacing-unit * 3;
    margin-bottom: $general-spacing-unit * 5;

    @include breakpoint ("small-tablet-bk") {
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.title {
    font-weight: 700;
    font-size: 24px;
    line-height: 18px;
    color: $color-cloud-burst;
    margin-bottom: $general-spacing-unit * 3;

    @include breakpoint ("mobile") {
        font-size: 18px;
        margin-bottom: $general-spacing-unit * 2;
    }
}

.description {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.details {
    display: flex;
    flex-direction: column;

    span {
        font-size: 16px;
        line-height: 21px;
        font-weight: 700;
        margin-right: $general-spacing-unit * 2.66;
        color: $color-cloud-burst;

        b {
            color: $color-eastern-blue;
        }
    }

    @include breakpoint ("mobile") {
        span {
            font-size: 13px;
            line-height: 18px;
        }
    }
}

.showDetails {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 167px;
    padding: 0;
    margin: 0;
    border-radius: 5px;
    font-weight: 700;
    font-size: 18px;
    text-align: center;
    flex: 1 1;
    text-decoration: none;
    box-sizing: border-box;
    white-space: nowrap;
    background: #1b9b9e;
    color: #fff;
    height: 56px;
    line-height: 52px;
    border: none;
    cursor: pointer;
    font-family: "Source Sans Pro";
    border: 2px solid #1b9b9e;

    @include breakpoint ("mobile") {
        max-width: 104px;
        line-height: 16px;
        font-size: 13px;
        height: 32px;
    }
}

.headerDetails {
    margin: 0 0 30px 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    button:nth-child(1) {
        background: transparent;
        border: 0;
        cursor: pointer;
    }

    @include breakpoint ("mobile") {
        display: none;
    }
}